import React from 'react'
import Layout from '../components/layout'

import './style/wedding-party.css'

import { BRIDESMAIDS, GROOMSMEN } from '../data/wedding-party.js'

const WeddingParty = (props) => (
    <Layout>
        <div className='wedding-party'>
            <div className='wedding-party-hero'>
                <div className='wedding-party-hero-img'>
                    <span className='wedding-party-hero-text'>
                        Wedding Party
                    </span>
                </div>
            </div>

            <div className='wedding-party-content'>
                <div className='party'>
                    <div className='party-header'>Bridesmaids</div>
                    {BRIDESMAIDS.map((bridesmaid, i) => (
                        <>
                            {bridesmaid.name === 'Madeline Li' && (
                                <div className='party-header' style={{marginTop: '20px'}}>Flower Girl</div>
                            )}
                            <div key={i} className={`person person-${i + 1}`}>
                                <div className='person-name'>{bridesmaid.name}</div>
                                <img className='person-photo' alt='bridesmaid' src={bridesmaid.photo} />
                            </div>
                        </>
                    ))}
                </div>

                <div className='party'>
                    <div className='party-header'>Groomsmen</div>
                    {GROOMSMEN.map((groomsman, i) => (
                        <>
                            {groomsman.name === 'Marcus Huang' && (
                                <div className='party-header' style={{marginTop: '20px'}}>Ring Bearer</div>
                            )}
                            <div key={i} className={`person person-${i + 1}`}>
                                <div className='person-name'>{groomsman.name}</div>
                                <img className='person-photo' alt='groomsman' src={groomsman.photo} />
                            </div>
                        </>
                    ))}
                </div>
            </div>
        </div>
    </Layout>
)

export default WeddingParty
